import { CardTypeGroup, DEFAULT_BAN_STATE_ARR, } from "../../../core/src/main";
import { computed, defineComponent, reactive, watch, } from "@vue/composition-api";
import VSelect from "vue-select";
import { applicationContainer } from "../inversify.config";
import { APPLICATION_TYPES } from "../types";
import { appStore } from "../composition/appStore";
const cardDatabase = applicationContainer.get(APPLICATION_TYPES.CardDatabase);
const banlistService = applicationContainer.get(APPLICATION_TYPES.BanlistService);
export default defineComponent({
    props: {
        filter: {
            required: true,
            type: Object,
        },
        showOnly: {
            required: false,
            type: Array,
            default: null,
        },
    },
    components: {
        VSelect,
    },
    model: {
        prop: "filter",
        event: "change",
    },
    setup(props, context) {
        const banStates = DEFAULT_BAN_STATE_ARR;
        const typeGroups = Object.values(CardTypeGroup);
        const internalFilter = reactive(props.filter);
        const sets = computed(() => cardDatabase.getSets());
        const archetypes = computed(() => cardDatabase.getArchetypes());
        const types = computed(() => internalFilter.typeGroup != null
            ? cardDatabase.getTypes(internalFilter.typeGroup)
            : []);
        const subTypes = computed(() => internalFilter.typeGroup != null
            ? cardDatabase.getSubTypes(internalFilter.typeGroup)
            : []);
        const attributes = computed(() => cardDatabase.getAttributes());
        const levels = computed(() => cardDatabase.getLevels());
        const linkMarkers = computed(() => cardDatabase.getLinkMarkers());
        const hasBanStates = computed(() => banlistService.hasFormatBanlist(appStore(context).state.format.active));
        const isMonster = computed(() => internalFilter.typeGroup === CardTypeGroup.MONSTER);
        const isFieldVisible = (fieldName) => props.showOnly == null || props.showOnly.includes(fieldName);
        const onFilterChanged = () => context.emit("change", internalFilter);
        watch(() => internalFilter.typeGroup, () => {
            internalFilter.type = undefined;
            internalFilter.subType = undefined;
            internalFilter.attribute = undefined;
            internalFilter.level = undefined;
            internalFilter.linkMarker = undefined;
        });
        watch(() => hasBanStates.value, () => {
            internalFilter.banState = undefined;
        });
        return {
            banStates,
            sets,
            archetypes,
            typeGroups,
            types,
            subTypes,
            attributes,
            levels,
            linkMarkers,
            internalFilter,
            hasBanStates,
            isMonster,
            onFilterChanged,
            isFieldVisible,
        };
    },
});
