import { computed } from "@vue/composition-api";
import { appStore } from "./appStore";
import { applicationContainer } from "../inversify.config";
import { APPLICATION_TYPES } from "../types";
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
export const deckEmpty = (context) => computed(() => deckService.getAllCards(appStore(context).state.deck.active)
    .length === 0);
