import { CardTypeGroup } from "../../../../core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import YgoCard from "../YgoCard.vue";
export default defineComponent({
    props: {
        card: {
            required: true,
            type: Object,
        },
    },
    components: {
        YgoCard,
    },
    setup(props) {
        const typeText = computed(() => props.card.type.group === CardTypeGroup.MONSTER
            ? props.card.type.name
            : props.card.type.group);
        const subTypeText = computed(() => props.card.type.group === CardTypeGroup.MONSTER
            ? `${props.card.attribute}/${props.card.subType}`
            : props.card.subType);
        return { typeText, subTypeText };
    },
});
