import { DECK_PART_ARR, } from "../../../../core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import YgoPrice from "../YgoPrice.vue";
import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import YgoDeckPart from "./YgoDeckPart.vue";
import { appStore } from "../../composition/appStore";
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
export default defineComponent({
    components: {
        YgoDeckPart,
        YgoPrice,
    },
    props: {
        canMove: {
            required: true,
            type: Function,
        },
    },
    setup(props, context) {
        const deckParts = DECK_PART_ARR;
        const allCards = computed(() => deckService.getAllCards(appStore(context).state.deck.active));
        return {
            deckParts,
            allCards,
        };
    },
});
