import { computed, defineComponent } from "@vue/composition-api";
import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import { appStore } from "../../composition/appStore";
import { deckEmpty } from "../../composition/deckEmpty";
const deckExportService = applicationContainer.get(APPLICATION_TYPES.DeckExportService);
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
export default defineComponent({
    components: {},
    props: {},
    setup: (props, context) => {
        const isDeckEmpty = deckEmpty(context);
        const buyLink = computed(() => deckExportService
            .toBuyLink(appStore(context).state.deck.active)
            .toString());
        return { isDeckEmpty, buyLink };
    },
});
