import { defineComponent } from "@vue/composition-api";
import { applicationContainer } from "../../../inversify.config";
import { APPLICATION_TYPES } from "../../../types";
import { BDropdownItem } from "bootstrap-vue";
import { copyText } from "../../../../../ui/src/main";
import { appStore } from "../../../composition/appStore";
const deckUriEncodingService = applicationContainer.get(APPLICATION_TYPES.DeckUriEncodingService);
export default defineComponent({
    components: { BDropdownItem },
    props: {},
    setup: (props, context) => {
        const copyYdke = () => {
            const deck = appStore(context).state.deck.active;
            const ydke = deckUriEncodingService.toUri(deck);
            copyText(ydke.toString(), document);
            context.root.$bvToast.toast("Successfully copied YDKe to clipboard!", {
                variant: "success",
                noCloseButton: true,
                toastClass: "deck-tool__portal",
            });
        };
        return { copyYdke };
    },
});
