import { SortingOrder, SortingStrategy, } from "../../../../core/src/main";
import { defineComponent, reactive } from "@vue/composition-api";
import VSelect from "vue-select";
export default defineComponent({
    props: {
        sortingOptions: {
            required: true,
            type: Object,
        },
    },
    components: {
        VSelect,
    },
    model: {
        prop: "sortingOptions",
        event: "change",
    },
    setup(props, context) {
        const internalSortingOptions = reactive(props.sortingOptions);
        const sortingStrategies = [
            SortingStrategy.LEVEL,
            SortingStrategy.ATK,
            SortingStrategy.DEF,
            SortingStrategy.NAME,
            SortingStrategy.RELEASE_DATE,
        ];
        const sortingOrders = [SortingOrder.DESC, SortingOrder.ASC];
        const onOptionsChanged = () => context.emit("change", internalSortingOptions);
        return {
            sortingStrategies,
            sortingOrders,
            internalSortingOptions,
            onOptionsChanged,
        };
    },
});
