import { defineComponent } from "@vue/composition-api";
import YgoBuilder from "../components/builder/YgoBuilder.vue";
import YgoDeck from "../components/deck/YgoDeck.vue";
import { applicationContainer } from "../inversify.config";
import { APPLICATION_TYPES } from "../types";
import { appStore } from "../composition/appStore";
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
// Workaround-ish solution to allow fetching the target deck part of a a drag event.
const findDeckPartForComponent = (el) => {
    let current = el;
    while (current.$parent != current.$root) {
        const deckPart = current.$props["deckPart"];
        if (deckPart != null) {
            return deckPart;
        }
        current = current.$parent;
    }
    return null;
};
export default defineComponent({
    components: {
        YgoDeck,
        YgoBuilder,
    },
    props: {},
    setup: (props, context) => {
        const canMoveInDeckParts = (e, oldDeckPart) => {
            const newDeckPart = findDeckPartForComponent(e.relatedContext.component);
            if (newDeckPart == null) {
                return false;
            }
            const deck = appStore(context).state.deck.active;
            const format = appStore(context).state.format.active;
            const card = e.draggedContext.element;
            return deckService.canMove(deck, oldDeckPart, newDeckPart, format, card);
        };
        const canMoveFromBuilder = (e) => {
            const newDeckPart = findDeckPartForComponent(e.relatedContext.component);
            if (newDeckPart == null) {
                return false;
            }
            const deck = appStore(context).state.deck.active;
            const format = appStore(context).state.format.active;
            const card = e.draggedContext.element;
            return deckService.canAdd(deck, newDeckPart, format, card);
        };
        return { canMoveInDeckParts, canMoveFromBuilder };
    },
});
