import { applicationContainer } from "../inversify.config";
import { APPLICATION_TYPES } from "../types";
import { DEFAULT_VENDOR_ARR, } from "../../../core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import { appStore } from "../composition/appStore";
import { BTooltip } from "bootstrap-vue";
import { uniqueId } from "lodash";
const priceService = applicationContainer.get(APPLICATION_TYPES.PriceService);
const cardService = applicationContainer.get(APPLICATION_TYPES.CardService);
export default defineComponent({
    components: { BTooltip },
    props: {
        cards: {
            required: true,
            type: Array,
        },
    },
    setup(props, context) {
        const activeCurrency = computed(() => appStore(context).state.currency.active);
        const priceByVendor = computed(() => new Map(DEFAULT_VENDOR_ARR.map((vendor) => {
            const lookupResult = priceService.getPrice(props.cards, vendor, activeCurrency.value);
            return [vendor, lookupResult];
        })));
        const listMissingCards = (lookupResult) => cardService.createFormattedCardCountList(cardService.countByCard(lookupResult.missing));
        const formatPrice = (lookupResult) => priceService.formatPrice(lookupResult.price, activeCurrency.value);
        const id = uniqueId("priceTooltip_");
        const createTooltipButtonId = (vendor) => `${id}_${vendor.id}`;
        return {
            priceByVendor,
            formatPrice,
            listMissingCards,
            createTooltipButtonId,
        };
    },
});
